@import "./reset.scss";
@import url('https://fonts.googleapis.com/css2?family=Open+Sans&display=swap');

* {
    box-sizing: border-box;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

body {
    background-color: #111;
    color: white;
    font-family: 'Open Sans', sans-serif;
}

@media screen and (max-width: 406px) {
    .container {
        margin: 0 auto;
        padding: 0px;
    }
}